import React from "react"
import cards from "./cards"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { Wrapper, Contents, Row, ButtonLink } from "../../common"
import { Info, Cards, Card, Heading, SubHeading } from "./styles"

function PaymentSolutions() {
  return (
    <Wrapper bgColor="#f5f5f5">
      <Contents>
        <Row>
          <Info>
            <Heading>Solues de pagamento para qualquer pessoa.</Heading>
            <SubHeading>
            Transferncias internacionais de dinheiro.
            Enviar dinheiro para sua conta no exterior
            Investir em outro pas
            Mandar dinheiro para algum da famlia
            Transferncia de dinheiro para um terceiro
            Enviar dinheiro para um estudante no exterior
            Pagar MBA, intercmbio, high-school ou outros cursos
            Pagar aluguel de imvel no exterior
            Pagar a compra de um imvel no exterior
            Receber uma transferncia de sua conta exterior
            Receber salrio do exterior
            Receber pagamento por um servio
            Envio de dinheiro para o exterior de um jeito fcil.
            </SubHeading>
            <ButtonLink fontSize="1.125rem" href="#">
             Encontre mais soluess{" "}
              <FontAwesomeIcon
                icon="chevron-right"
                transform="down-1 shrink-3 right-4"
              />
            </ButtonLink>
          </Info>
          <Cards>
            {cards.map((card, index) => (
              <Card
                key={index}
                content={card.alt}
                image={card.image}
                aria-label={card.alt}
              />
            ))}
          </Cards>
        </Row>
      </Contents>
    </Wrapper>
  )
}

export default PaymentSolutions
